<script>
import Header from '../../../components/assistant/Header.vue'
export default {
  name: 'app',
  mounted () {
    this.loadData()
  },
  components: {
    Header
  },
  watch: {
    'formBindPhone.phone': {
      deep: true,
      handler: function () {
        var reg = /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/
        if (this.formBindPhone.phone === null || this.formBindPhone.phone === undefined || !reg.test(this.formBindPhone.phone)) {
          this.canGetVerify = false
        } else {
          this.canGetVerify = true
        }
      }
    }
  },
  data () {
    var validatePhone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入手机号'))
      } else {
        if (this.formBindPhone.phone !== '') {
          var reg = /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/
          if (!reg.test(this.formBindPhone.phone)) {
            callback(new Error('手机号格式不正确'))
          }
        }
        callback()
      }
    }
    var filterPhone = (rule, value, callback) => {
      this.formBindPhone.phone = value.replace(/[^\d]/g, '')
      callback()
    }
    var filterVerify = (rule, value, callback) => {
      this.formBindPhone.verify = value.replace(/[^\d]/g, '')
      callback()
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.formPwd.newPwd !== '') {
          if (this.formPwd.newPwd !== this.formPwd.confirmPwd) {
            callback(new Error('新密码和确认密码输入必须一致！'))
          }
        }
        callback()
      }
    }
    var filterPass = (rule, value, callback) => {
      var patrn = /^(\w){6,20}$/
      if (!patrn.exec(value)) {
        callback(new Error('只能输入6-20个字母、数字、下划线 '))
      }
      callback()
    }
    return {
      msg: '',
      canGetVerify: false,
      level: '', // 密码强度等级
      verifyCodeText: '获取验证码', // 验证码按钮显示内容
      interval: null, // 验证码码定时器
      countdown: '', // 验证码倒计时秒数
      countdownshow: false, // 验证码倒计时秒数显示标志
      userPhone: '', // 用户此时的手机号
      userImg: '', // 用户的头像
      userNickname: '', // 用户昵称
      bindPhoneFlg: false, // 绑定手机区域显示flg
      resetPwdFlg: false, // 修改密码区域显示flg
      formBindPhone: {
        phone: '',
        verify: ''
      },
      formPwd: {
        oldPwd: '',
        newPwd: '',
        confirmPwd: ''
      },
      rules: {
        phone: [
          { min: 11, max: 11, message: '手机号长度应为11位', trigger: 'blur' },
          { validator: validatePhone, trigger: 'blur' },
          { validator: filterPhone, trigger: 'change' }
        ],
        verify: [
          { validator: filterVerify, trigger: 'change' },
          { required: true, message: '请输入短信验证码', trigger: 'blur' }
        ],
        oldPwd: [
          { required: true, message: '请输入旧密码', trigger: 'blur' },
          { validator: filterPass, trigger: 'change' }
        ],
        newPwd: [
          { validator: validatePass, trigger: 'blur' },
          { validator: filterPass, trigger: 'change' }
        ],
        confirmPwd: [
          { validator: validatePass, trigger: 'blur' },
          { validator: filterPass, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    loadData () {
      let me = this
      this.$http.post('/user/queryuserinfo', {}, res => {
        if (res.success) {
          console.log(res.data)
          me.userPhone = res.data.userCellphone.slice(0, 3) + '****' + res.data.userCellphone.slice(7, 11)
          me.userImg = res.data.userPhotoUrl
          me.userNickname = res.data.userNickname
        }
      })
    },
    onSuccess () {
      let self = this
      this.msg = '验证成功,请注意查收短信。'
      this.countdown = 60
      this.countdownshow = true
      // 开始计时
      this.interval = setInterval(() => {
        this.countdown--
        if (this.countdown <= 0) {
          this.verifyCodeText = '重新发送'
          this.countdownshow = false
          clearInterval(this.interval)
        }
      }, 1000)
      this.$http.post('/user/vcfraudprevent', {}, res => {
        if (res.success) {
          self.$http.post('/user/sendsmsverificationcode', {
            'phone': self.formBindPhone.phone,
            'msgType': 'modcellphone',
            'sign': res.data.sign
          }, res => {
            if (res.success) {
            } else {
              self.$alert('提示', res.errorMsg)
              this.verifyCodeText = '重新发送'
              this.countdownshow = false
              clearInterval(this.interval)
            }
          })
        } else {
        }
      })
    },
    onFail () {
      this.msg = ''
    },
    onRefresh () {
      console.log('ok----')
    },
    change (num) {
      console.log('num', num)
    },
    updateBindPhone (formName) {
      let me = this
      console.log(me.formBindPhone.phone)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          me.$http.post('/user/modifycellphone', {
            'newCellphone': me.formBindPhone.phone,
            'smsVerificationCode': me.formBindPhone.verify
          }, res => {
            if (res.success) {
              // console.log(res.data)
              me.$message('绑定成功')
              window.location.reload()
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    updatePwd (formName) {
      let me = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          me.$http.post('/user/modifypassword', {
            oldPassword: me.formPwd.oldPwd,
            newPassword: me.formPwd.newPwd
          }, res => {
            if (res.success) {
              me.$message('修改密码成功')
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    openBindPhoneBox () {
      this.bindPhoneFlg = !this.bindPhoneFlg
    },
    openUpdatePwdBox () {
      this.resetPwdFlg = !this.resetPwdFlg
    }
  }
}
</script>

